import {Link} from "react-router-dom";
import React from 'react'
import AboutView from "./MainView/AboutView";
import ProductView from "./MainView/ProductView";


export const navItems=[
  {path:"/menu",exact:false,name:"Menu St Valentin",component:ProductView},
  {path:"/",exact:true,name:"Le Concept",component:AboutView},
];

export default function NavBar() {
  
    return (
        <nav>
          <ul className="center main-nav">
            {navItems.map(({path,exact,name})=> {return(<li><Link exact={exact} to={path}>{name}</Link></li>)})}
          </ul>
        </nav>
    )
}